<template>
  <article class="project-info">
    <main class="project">
      <figure class="project">
        <img :src="project.image">
      </figure>

      <div>
        <h2>{{ project.name }}</h2>
        <p>{{ project.description }}</p>
        <p>
          <strong>Copy link for demo:</strong>
          {{ project.link }}
        </p>
      </div>
    </main>
    <section class="project">
      <h2>The Tech Toolbox used:</h2>
      <section class="tech">
        <h1>{{ project.tools }}</h1>
      </section>
    </section>
  </article>
</template>

<script>
import axios from "axios";

export default {
  name: "ProjectInfo",
  data() {
    return {
      project: []
    };
  },
  created() {
    this.getProjectById();
  },
  methods: {
    getProjectById() {
      let projectId = this.$route.params.id;
      axios
        .get("/projects.json")
        .then(response => {
          this.project = response.data.find(r => r.id === projectId);
        })
        .catch(error => console.log(error));
    }
  }
};
</script>

