<template>
  <article class="cv">
    <header class="headline">
      <h1>Curriculum vitae</h1>
      <p>Besides being a very creative spirit I am also a digital craftsman with a technical background. As a responsible and competent person being a very selv driven employee is one of my strongest competences. In my personal life I am happily married to Peter and at the age of 42 I am lucky to have gotten the exactly two daughters I want.</p>
      <h3>A sum up of what I have been doing with my life:</h3>

      <br>
    </header>
    <table>
      <tr v-for="r in resume" :key="r.id">
        <td class="LH">{{ r.year }}</td>
        <td class="RH">
          <strong>{{ r.company }}</strong>
          <p>Main focus:</p>
          <h4>{{ r.job }}</h4>
          <br>
          <p class="pq">{{ r.quote }}</p>
        </td>
      </tr>
    </table>
  </article>
</template>


<script>
import axios from "axios";

export default {
  name: "Resume",
  data() {
    return {
      resume: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("/resume.json")
        .then(response => (this.resume = response.data))
        .catch(error => console.log(error));
    }
  }
};
</script>