<template>
  <article>
    <section class="semesters">
      <section class="case" v-for="project in projects" :key="project.id">
        <router-link :to="`/project/${project.id}`">
          <img :src="project.image">
        </router-link>
      </section>
    </section>
  </article>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      projects: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("/projects.json")
        .then(response => (this.projects = response.data))
        .catch(error => console.log(error));
    }
  }
};
</script>